
.details{
    opacity: 0.9;
    padding: 60px 20px;
    flex: 1;
    color: white;
    background: #252627
}

.details-name{
    font-size: 65px;
    font-weight: bold;
}

.details-shortdes{
    font-size: 20px
}

.details-holder{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 100vh;
    background: #252627
}
.background-section{
    background: url('../assets/bg.jpg');
    background-size: cover;
    flex: 1;
    height: 100vh;
}

@media only screen and (max-width: 600px) {
    .details-holder{
        display: flex;
        align-items: center;
        height: 90vh;
        background: url('../assets/bg.jpg');
        background-size: cover;
    }
    .background-section{
        background: none;
        flex: none;
        height: 0px;
    }
}